.App {
    align-items: center;
    background-color: #282c34;
    color: white;
    display: flex;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    justify-content: center;
    min-height: 100vh;
    text-align: center;
}

.Page {
    width: 80vw;
}

button {
    background-color: #4caf50;
    border: none;
    border-radius: 2em;
    cursor: pointer;
    color: white;
    font-size: 1em;
    margin: 8px 0;
    padding: 0.5em 1em;
}

button.socialBtn {
    background-color: aquamarine;
    color: black;
}

button.relaxBtn {
    background-color: blanchedalmond;
    color: black;
}

button.pplBtn {
    background-color: white;
    color: black;
    font-size: 0.6em;
}

hr {
    margin: 20px 0;
    border: none;
    border-top: 1px solid #eee;
    width: 15em;
}

b {
    margin-top: 1em;
}

ol {
    text-align: left;
    font-size: medium;
}

input {
    font-size: large;
    text-align: center;
    width: 25%;
}
